.dropdownsWrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.dropdownsWrapper select {
    /* appearance: none; */
    padding: 12px 20px 14px 20px;
    outline: none;
    border: 1px solid rgba(255, 255, 255, 0.6);
    color: rgba(255, 255, 255, 0.6);
    background-color: rgba(74, 86, 226, 0.1);
    box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    cursor: pointer;
    transition: all 0.5s ease 0s;
}

.dropdownsWrapper select option {
    background-color: rgba(158, 64, 186, 0.5);
    box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.25);
    color: rgba(255, 255, 255, 0.6);

    background-color: rgba(106, 70, 165, 0.525);
    background-color: rgba(80, 48, 154, 0.7);
    /* background-color: rgba(83, 61, 186, 0.7); */
}

.dropdownsWrapper select:hover {
    color: rgba(251, 251, 251, 1);
    border: 1px solid rgba(251, 251, 251, 1);
    transition: all 0.5s ease 0s;
}

.dropdownsWrapper select:focus {
    border: 1px solid rgba(255, 134, 141, 1);
    transition: all 0.5s ease 0s;
    color: rgba(255, 134, 141, 1);
    font-weight: bold;
}

@media screen and (min-width: 768px) {
    .dropdownsWrapper {
        flex-direction: row;
        gap: 16px;
    }

    .dropdownsWrapper select {
        flex: 1;
    }
}

@media screen and (min-width: 1280px) {
    .dropdownsWrapper {
        gap: 32px;
    }
}